import companyModel from "@/model/company"

import CuiPullrefresh from "@/components/cui-vue/cui-pullrefresh/CuiPullrefresh";
import CuiLoading from "@/components/cui-vue/cui-loading/CuiLoading";
export default {
    name: "Company",
    components:{
        CuiPullrefresh,
        CuiLoading
    },
    data()
    {
        return {
            data:[],//渲染的数据
            isShow:false,//是否展示（为了过渡动画）
            p:1,
            lock:false,
            isLoading:true,
            showVideo:false,
            video_url:""
        }
    },
    props:{
        //接收父组件的值
        //是否更新
        update:{
            default()
            {
                return false;
            }
        },
        //一页的个数
        length:{
            default()
            {
                return 5;
            }
        },
        //筛选条件
        filterData:{
            default()
            {
                return {};
            }
        }
    },
    computed:{

    },
    methods:{
        //获取职位数据
        getData(cb=()=>{})
        {
            let that=this,
                callback=cb,
                //复制一份 防止引用类型的影响
                filter=JSON.parse(JSON.stringify(that.filterData));
            Object.assign(filter,{p:this.p,length:this.length})
            //网络请求公司数据（目前先模拟）
            companyModel.getCompany(filter,(ret)=>{
                if(that.p===1)
                {
                    that.data=ret;
                }
                else
                {
                    that.data=Array.prototype.concat(that.data,ret);
                }
                callback(ret&&ret instanceof Array?ret.length:0)
            })
        },
        //滑动到底部的回调
        down(ret)
        {
            let that=this;
            that.p=that.p+1;
            ret.loading();
            that.getData((n)=>{
                if(!n)
                {
                    ret.nothing();
                }
                else
                {
                    ret.noloading();
                }

                that.$nextTick(()=>{
                    if(!that.lock)
                    {
                        ret.openLock();
                    }
                });
            });
        },
        //下拉刷新，被调用 done() 可以收起下拉动画
        refresh(done) {
            let that = this;
            that.isLoading=false;
            //that.isShow=false;
            setTimeout(function () {
                that.p = 1;

                that.getData(()=>{
                    that.$nextTick(()=>{
                        that.isShow=true;
                        that.isLoading=false;
                        done();
                    })
                })
            }, 1000)
        },
        playVideo(video)
        {
            if(video)
            {
                this.video_url=video;
                this.showVideo=true;
                this.$router.push({path:"/playvideo",query:{
                        url:this.video_url
                    }})
            }
        },
        closeVideo()
        {
            this.showVideo=false;
        }
    },
    watch:{
        //监听update的开关
        update:function(newVal)
        {
            let that=this;
            //关锁 告诉父组件您要关锁了
            that.lock=true;
            that.isShow=false;
            that.data=[];
            that.isLoading=true;
            that.getData(()=>{
                //开锁 告诉父组件您可以开锁了
                that.$nextTick(()=>{
                    that.isShow=true;
                    that.lock=false;
                    that.isLoading=false;
                })
            });
        }
    },
    created() {
        let that=this;
        that.lock=true;
        that.getData(()=>{
            that.isShow=true;
            //开锁 告诉父组件您可以开锁了
            that.$nextTick(()=>{
                that.lock=false;
                that.isLoading=false;
            })
        });
    }
}



